import React from 'react';
import { Layout,Menu,Icon } from 'antd';
import { withRouter } from 'react-router-dom';

const { Sider } = Layout;

function DashBoard({history,children}) {
    function handleClick(item){
        const path = {
            'map':'/map',
            'industry':'/industry'
        }
        history.push('/dashboard'+path[item.key]);
    }

    return (
        <Layout style={{background:'none' }}>
            <Sider width={200} style={{ background: 'none'}}>
                <Menu mode="inline" theme="dark" style={{ height: "calc(100vh - 74px)",borderRight: 0,background: 'rgba(255,255,255,0.1)'}} defaultSelectedKeys={['map']} onClick={handleClick}>
                    <Menu.Item key="map"><Icon type="global"></Icon>地域分析</Menu.Item>
                    <Menu.Item key="industry"><Icon type="global"></Icon>产业分析</Menu.Item>
                </Menu>
            </Sider>
            <Layout style={{ margin: '0 5px',background:'rgba(255,255,255,0.1)', height: "calc(100vh - 74px)",padding:24}}>               
                {children}  
            </Layout>
        </Layout>
    );
}

export default withRouter(DashBoard);