import React,{useEffect}from 'react';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/map';
import 'echarts/lib/component/visualMapContinuous';
import 'echarts/lib/component/tooltip';
import 'echarts/map/js/china';

const ChinaMap = ({ area,maxData }) => {

    const drawMap = (area,maxData) => {
        // 基于准备好的DOM，初始化echarts实例
        const myChart = echarts.init(document.getElementById('chinaMap'));
        // 指定图标配置项和数据
        const option = {
            tooltip: {
                trigger: 'item'
            },
            legend: {
                show: false
            },
            grid:{
                left:0,
                top:10
            },
            visualMap: {
                min: 0,
                max: maxData,
                text: ['高','低'], // 文本，默认为数值文本
                textStyle:{
                    color:'white'
                },
                color:['#BDE8FF','#9EC9FF','#7EABFF','#5A8DF8','#3874DB','#005BBF','#0044A4','#122C6A'].reverse()
            },
            toolbox: {
                show: false
            },
            series: [{
                name: 'chinaMap',
                type: 'map',
                map: 'china',
                label: {
                    position: 'inside',
                    color:'#fff'
                },
                showLegendSymbol: false,
                data: area,
                zoom:1.2,
                itemStyle:{
                    areaColor:'rgba(255,255,255,0.2)',
                    borderColor:'#1890ff',
                    borderWidth:0.5,
                    shadowBlur:1,
                    shadowColor:'#1890ff'
                    
                }
            }]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
    }

    useEffect(()=>{
        drawMap(area,maxData)
    })
    return (
        <div id="chinaMap" style={{height:"90%",width:"100%"}}>
        </div>
    )
}

export default ChinaMap;