import React,{ useState,useEffect } from 'react';
import {Layout} from 'antd';
import BubbleMap from '../BubbleMap';
import axios from 'axios';

const { Content } = Layout;

const Industry = () => {
    const [industryName,setIndustryName] = useState([])
    const [instiName,setInstiName] = useState([])
    const [dataset,setDataSet] = useState([])

    useEffect(()=>{
        axios.get('../data/industryData.csv').then(response=>{
            let data = response.data.split('\r').slice(0,51);
            let industryName = data[0].split(",").slice(1,-1);
            let instiName = data.slice(1).map(d=>d.split(",")[0])
            let dataset = data.slice(1).reduce((acc,cur,index)=>{
                return acc.concat(cur.split(",").slice(1).map((d,i)=>[i,index,(+d)/5]))
            },[])
            setIndustryName(industryName)
            setInstiName(instiName)
            setDataSet(dataset)
        })
    },[])
    
    return (
        <Content style={{ background: 'rgba(255,255,255,0)' }}>       
            <BubbleMap instiName= {instiName} industryName={industryName} dataset={dataset}></BubbleMap>
        </Content>
    )

}

export default Industry;