import React,{useEffect} from 'react';
import echarts from 'echarts/lib/echarts';
import 'echarts/lib/chart/scatter';
import 'echarts/lib/component/axis';
import 'echarts/lib/component/grid';
import 'echarts/lib/component/tooltip';

const BubbleMap =({instiName,industryName,dataset})=>{
    
    const drawBubbleMap = (instiName,industryName,dataset)=>{
        // 基于准备好的DOM，初始化echarts实例
        const myChart = echarts.init(document.getElementById('bubbleMap'));
        const color =['#5B8FF9','#5AD8A6','#FF99C3','#F6BD16','#E8684A','#6DC8EC','#9270CA','#FF9D4D','#269A99']
        // 指定图标配置项和数据
        const option = {
            tooltip: {
                position: 'top',
                formatter: function (params) {
                    return params.value[2]*5 + '个 ' + industryName[params.value[0]] + ' of ' + instiName[params.value[1]];
                }
            },
            grid: {
                left: 0,
                bottom: 10,
                top:10,
                right: 40,
                containLabel: true
            },
            xAxis: {
                type: 'category',
                position:'top',
                data: industryName,
                boundaryGap: false,
                splitLine: {
                    show: true,
                    lineStyle: {
                        color: '#999',
                        type: 'dashed'
                    }
                },
                axisLine: {
                    show: false
                },
                axisLabel:{
                    color:'#fff',
                    interval:0
                }
            },
            yAxis: {
                type: 'category',
                data: instiName,
                axisLine: {
                    show: false
                },
                axisLabel:{
                    color:'#fff'
                }
            },
            series: [{
                name: '产业分析',
                type: 'scatter',
                symbolSize: function (val) {
                    if(val[2]<=8 && val[2]>0){
                        return 8
                    }
                    if(val[2]>=50){
                        return 50
                    }
                    else{
                        return val[2];
                    }
                },
                data: dataset,
                animationDelay: function (idx) {
                    return idx * 2;
                },
                itemStyle:{
                    color:function(params){
                        return color[params.value[0]]
                    }
                }
            }]
        }
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
    }
    useEffect(()=>{
        drawBubbleMap(instiName,industryName,dataset)  
    })
    
    return (
        <div id="bubbleMap" style={{height:"500%",width:"100%"}}>
        </div>
    )

}

export default BubbleMap;