import React,{ useState } from 'react';
import ChinaMap from '../ChinaMap';
import { Layout,Select } from 'antd';

const { Content } = Layout;
const { Option } = Select;

const Map = () => {
    let dataset = JSON.parse(window.localStorage.getItem('data'))

    let selectData = [...new Set(dataset.map(item => item[1]))]
    const options = selectData.map(item => (
        <Option key={item}>{item}</Option>
    ))

    const filterData = value => {
        return dataset.filter(data => data[1] === value);
    }
    const [area,setArea] = useState(filterData(selectData[0]).map((data,index) => (
        {
            name: data[0],
            value: +data[2],
            label: {
                show: true,
                formatter: '{b}'
            }
        }
    )));

    const [maxData,setMaxData] = useState(Math.max.apply(null,filterData(selectData[0]).map(data => +data[2])));

    const onChange = value => {
        let categoryData = filterData(value)
        let areaData = categoryData.map((data,index) => (
            {
                name: data[0],
                value: +data[2],
                label: {
                    show: true,
                    formatter: '{b}'
                }
            }
        ))
        let maxData = Math.max.apply(null,categoryData.map(data => +data[2]))
        setArea(areaData)
        setMaxData(maxData)
    }

    return (
        <Content style={{ background: 'rgba(255,255,255,0)',height:'100%'}}>
            <Select showSearch defaultValue={selectData[0]} style={{ width: 200 }} placeholder="选择分类" optionFilterProp="children" onChange={onChange} filterOption={(input,option) => option.props.children.indexOf(input) >= 0}>
                {options}
            </Select>
            <ChinaMap area={area} maxData={maxData}></ChinaMap>
        </Content>
    )

}
export default Map;