import React,{ useState } from 'react';
import { Upload,Icon,message,Button } from 'antd';
import { withRouter } from 'react-router-dom';
const { Dragger } = Upload;

function Uploader({ history }) {
  const [disabled,setDisabled] = useState(true)
  const props = {
    name: 'file',
    accept: '.csv',
    multiple: false,
    action: file => {
      return new Promise(resolve => {
        const reader = new FileReader();
        reader.readAsText(file,'utf8');
        reader.onload = evt => {
          const dataset = evt.target.result.split('\r\n');
          const dataArr = dataset.slice(1).map(data => data.split(','))
          const storage = window.localStorage;
          storage.setItem('data',JSON.stringify(dataArr));
          setDisabled(false)
          message.success(`${file.name} file uploaded successfully.`);
        }
      })
    }
  };
  function handleClick(){
    history.push('/dashboard/map');
  }
  return (
    <div>
      <Dragger {...props} style={{textAlign: 'center',margin:'100px auto',width:'90vw',background:'rgba(255,255,255,0.1)'}}>
        <p className="ant-upload-drag-icon" style={{lineHeight: '10vw'}}>
          <Icon type="inbox" />
        </p>
        <p className="ant-upload-text" style={{ lineHeight: '5vw',color:'white'}}>点击或者拖拽文件到这个区域上传</p>
        <p className="ant-upload-hint" style={{ lineHeight: '5vw',color:'white'}}>
          支持csv格式文件上传，文件上传完毕后，点击按钮，进行文件数据分析，赶紧尝试一下吧
        </p>
      </Dragger>
      <p style={{textAlign: 'center',margin:'0px auto'}}><Button type="primary" disabled={disabled} onClick ={handleClick}>进入DashBoard分析</Button></p>
    </div>
  );

}
export default withRouter(Uploader)

